import { Activity } from '../parser/xaml-parser';

/**
 * „ÉÑ„É™„Éº„Éì„É•„Éº„É¨„É≥„ÉÄ„É©„Éº
 */
export class TreeViewRenderer {
  /**
   * „Ç¢„ÇØ„ÉÜ„Ç£„Éì„ÉÜ„Ç£„ÉÑ„É™„Éº„Çí„ÉÑ„É™„ÉºË°®Á§∫„Å®„Åó„Å¶„É¨„É≥„ÉÄ„É™„É≥„Ç∞
   */
  render(parsedData: any, container: HTMLElement): void {
    container.innerHTML = '';                   // „Ç≥„É≥„ÉÜ„Éä„Çí„ÇØ„É™„Ç¢

    // „É´„Éº„Éà„Ç¢„ÇØ„ÉÜ„Ç£„Éì„ÉÜ„Ç£„Åã„Çâ„ÉÑ„É™„Éº„ÇíÁîüÊàê
    const tree = this.createTreeNode(parsedData.rootActivity, 0);
    container.appendChild(tree);
  }

  /**
   * „ÉÑ„É™„Éº„Éé„Éº„Éâ„Çí‰ΩúÊàê
   */
  private createTreeNode(activity: Activity, depth: number): HTMLElement {
    const treeItem = document.createElement('div');
    treeItem.className = 'tree-item';
    treeItem.dataset.id = activity.id;          // „Éá„Éº„ÇøÂ±ûÊÄß„Å´ID„ÇíË®≠ÂÆö
    treeItem.style.paddingLeft = `${depth * 20}px`; // „Ç§„É≥„Éá„É≥„Éà

    // Â±ïÈñã/Êäò„Çä„Åü„Åü„Åø„Éú„Çø„É≥
    const hasChildren = activity.children.length > 0;
    const toggleBtn = document.createElement('span');
    toggleBtn.className = 'tree-toggle';
    toggleBtn.textContent = hasChildren ? '‚ñº' : '  '; // Â≠ê„Åå„ÅÇ„ÇãÂ†¥Âêà„ÅØ‚ñº
    toggleBtn.style.cursor = hasChildren ? 'pointer' : 'default';

    // „É©„Éô„É´
    const label = document.createElement('span');
    label.className = 'tree-label';
    label.textContent = `${this.getActivityIcon(activity.type)} ${activity.displayName}`;

    // „Éé„Éº„Éâ„Çí„ÇØ„É™„ÉÉ„ÇØ„Åó„Åü„Çâ„É°„Ç§„É≥„Éì„É•„Éº„Åß„Éè„Ç§„É©„Ç§„Éà
    label.addEventListener('click', () => {
      this.highlightActivity(activity.id);
    });

    treeItem.appendChild(toggleBtn);
    treeItem.appendChild(label);

    // Â≠ê„Éé„Éº„Éâ„ÅÆ„Ç≥„É≥„ÉÜ„Éä
    if (hasChildren) {
      const childrenContainer = document.createElement('div');
      childrenContainer.className = 'tree-children';

      activity.children.forEach(child => {
        const childNode = this.createTreeNode(child, depth + 1);
        childrenContainer.appendChild(childNode);
      });

      treeItem.appendChild(childrenContainer);

      // Â±ïÈñã/Êäò„Çä„Åü„Åü„Åø„ÅÆ„Ç§„Éô„É≥„Éà
      toggleBtn.addEventListener('click', () => {
        const isExpanded = !childrenContainer.classList.contains('collapsed');
        childrenContainer.classList.toggle('collapsed', isExpanded);
        toggleBtn.textContent = isExpanded ? '‚ñ∂' : '‚ñº'; // „Ç¢„Ç§„Ç≥„É≥Â§âÊõ¥
      });
    }

    return treeItem;
  }

  /**
   * „É°„Ç§„É≥„Éì„É•„Éº„ÅßË©≤ÂΩì„Ç¢„ÇØ„ÉÜ„Ç£„Éì„ÉÜ„Ç£„Çí„Éè„Ç§„É©„Ç§„Éà
   */
  private highlightActivity(activityId: string): void {
    // Êó¢Â≠ò„ÅÆ„Éè„Ç§„É©„Ç§„Éà„ÇíÂâäÈô§
    document.querySelectorAll('.activity-card.highlighted').forEach(el => {
      el.classList.remove('highlighted');
    });

    // Ë©≤ÂΩì„Ç¢„ÇØ„ÉÜ„Ç£„Éì„ÉÜ„Ç£„Å´„Éè„Ç§„É©„Ç§„Éà„ÇíËøΩÂä†
    const targetCard = document.querySelector(`.activity-card[data-id="${activityId}"]`);
    if (targetCard) {
      targetCard.classList.add('highlighted');
      targetCard.scrollIntoView({ behavior: 'smooth', block: 'center' }); // „Çπ„ÇØ„É≠„Éº„É´
    }
  }

  /**
   * „Ç¢„ÇØ„ÉÜ„Ç£„Éì„ÉÜ„Ç£„Çø„Ç§„Éó„Å´Âøú„Åò„Åü„Ç¢„Ç§„Ç≥„É≥„ÇíÂèñÂæó
   */
  private getActivityIcon(type: string): string {
    const iconMap: Record<string, string> = {
      'Sequence': 'üîÑ',
      'Flowchart': 'üìä',
      'Assign': 'üìù',
      'If': 'üîÄ',
      'While': 'üîÅ',
      'ForEach': 'üîÅ',
      'Click': 'üñ±Ô∏è',
      'TypeInto': '‚å®Ô∏è',
      'GetText': 'üìÑ',
      'LogMessage': 'üìã',
      'InvokeWorkflowFile': 'üì§',
      'TryCatch': '‚ö†Ô∏è',
      'Delay': '‚è±Ô∏è'
    };

    return iconMap[type] || 'üì¶';               // „Éá„Éï„Ç©„É´„Éà„Ç¢„Ç§„Ç≥„É≥
  }
}
