/**
 * UiPath XAML „Éë„Éº„Çµ„Éº
 * XAML„Éï„Ç°„Ç§„É´„Çí„Éë„Éº„Çπ„Åó„Å¶ÊßãÈÄ†Âåñ„Éá„Éº„Çø„Å´Â§âÊèõ
 */

// UiPathÂõ∫Êúâ„ÅÆÂêçÂâçÁ©∫ÈñìÔºàÂ∞ÜÊù•„ÅÆÊã°ÂºµÁî®„Å´ÂÆöÁæ©Ôºâ
// const UIPATH_NS = 'http://schemas.uipath.com/workflow/activities';
// const XAML_NS = 'http://schemas.microsoft.com/winfx/2006/xaml';
// const ACTIVITIES_NS = 'http://schemas.microsoft.com/netfx/2009/xaml/activities';

/**
 * „Ç¢„ÇØ„ÉÜ„Ç£„Éì„ÉÜ„Ç£„ÅÆÂûãÂÆöÁæ©
 */
export interface Activity {
  id: string;                                   // ‰∏ÄÊÑè„ÅÆID
  type: string;                                 // „Ç¢„ÇØ„ÉÜ„Ç£„Éì„ÉÜ„Ç£„Çø„Ç§„ÉóÔºàSequence, AssignÁ≠âÔºâ
  displayName: string;                          // Ë°®Á§∫Âêç
  namespace?: string;                           // ÂêçÂâçÁ©∫Èñì„Éó„É¨„Éï„Ç£„ÉÉ„ÇØ„Çπ
  properties: Record<string, any>;              // „Éó„É≠„Éë„ÉÜ„Ç£„ÅÆÈÄ£ÊÉ≥ÈÖçÂàó
  children: Activity[];                         // Â≠ê„Ç¢„ÇØ„ÉÜ„Ç£„Éì„ÉÜ„Ç£
  annotations?: string;                         // „Ç¢„Éé„ÉÜ„Éº„Ç∑„Éß„É≥
  informativeScreenshot?: string;               // „Çπ„ÇØ„É™„Éº„É≥„Ç∑„Éß„ÉÉ„Éà„Éï„Ç°„Ç§„É´Âêç
}

/**
 * „Éë„Éº„ÇπÁµêÊûú„ÅÆÂûãÂÆöÁæ©
 */
export interface ParsedXaml {
  rootActivity: Activity;                       // „É´„Éº„Éà„Ç¢„ÇØ„ÉÜ„Ç£„Éì„ÉÜ„Ç£
  variables: Variable[];                        // Â§âÊï∞„É™„Çπ„Éà
  arguments: Argument[];                        // ÂºïÊï∞„É™„Çπ„Éà
}

/**
 * Â§âÊï∞„ÅÆÂûãÂÆöÁæ©
 */
export interface Variable {
  name: string;                                 // Â§âÊï∞Âêç
  type: string;                                 // Âûã
  default?: string;                             // „Éá„Éï„Ç©„É´„ÉàÂÄ§
}

/**
 * ÂºïÊï∞„ÅÆÂûãÂÆöÁæ©
 */
export interface Argument {
  name: string;                                 // ÂºïÊï∞Âêç
  type: string;                                 // ÂûãÔºàIn/Out/InOutÔºâ
  dataType: string;                             // „Éá„Éº„ÇøÂûã
}

/**
 * XAML„Éë„Éº„Çµ„Éº„ÇØ„É©„Çπ
 */
export class XamlParser {
  private activityIdCounter: number = 0;        // „Ç¢„ÇØ„ÉÜ„Ç£„Éì„ÉÜ„Ç£IDÁîüÊàêÁî®„Ç´„Ç¶„É≥„Çø„Éº

  /**
   * XAML„ÉÜ„Ç≠„Çπ„Éà„Çí„Éë„Éº„Çπ„Åó„Å¶ÊßãÈÄ†Âåñ„Éá„Éº„Çø„Å´Â§âÊèõ
   */
  parse(xamlText: string): ParsedXaml {
    // XML„Çí„Éë„Éº„Çπ
    const parser = new DOMParser();
    const xmlDoc = parser.parseFromString(xamlText, 'text/xml');

    // „Éë„Éº„Çπ„Ç®„É©„Éº„ÉÅ„Çß„ÉÉ„ÇØ
    const parseError = xmlDoc.querySelector('parsererror');
    if (parseError) {
      throw new Error('XAMLËß£Êûê„Ç®„É©„Éº: ' + parseError.textContent);
    }

    // „É´„Éº„ÉàË¶ÅÁ¥†„ÇíÂèñÂæó
    const root = xmlDoc.documentElement;

    // Â§âÊï∞„Å®ÂºïÊï∞„ÇíÊäΩÂá∫
    const variables = this.extractVariables(root);
    const argumentsData = this.extractArguments(root);

    // „É´„Éº„Éà„Ç¢„ÇØ„ÉÜ„Ç£„Éì„ÉÜ„Ç£„Çí„Éë„Éº„Çπ
    const rootActivity = this.parseActivity(root);

    return {
      rootActivity,
      variables,
      arguments: argumentsData
    };
  }

  /**
   * „Ç¢„ÇØ„ÉÜ„Ç£„Éì„ÉÜ„Ç£Ë¶ÅÁ¥†„Çí„Éë„Éº„Çπ
   */
  private parseActivity(element: Element): Activity {
    const id = `activity-${this.activityIdCounter++}`;
    const type = element.localName;              // Ë¶ÅÁ¥†ÂêçÔºàSequence, AssignÁ≠âÔºâ
    const displayName = element.getAttribute('DisplayName') || type;
    const namespace = element.prefix || undefined;

    // „Éó„É≠„Éë„ÉÜ„Ç£„ÇíÊäΩÂá∫
    const properties = this.extractProperties(element);

    // InformativeScreenshotÂ±ûÊÄß„ÇíÂèñÂæó
    const informativeScreenshot = element.getAttribute('InformativeScreenshot') || undefined;

    // „Ç¢„Éé„ÉÜ„Éº„Ç∑„Éß„É≥„ÇíÂèñÂæó
    const annotations = this.extractAnnotations(element);

    // Â≠ê„Ç¢„ÇØ„ÉÜ„Ç£„Éì„ÉÜ„Ç£„Çí„Éë„Éº„Çπ
    const children = this.parseChildren(element);

    return {
      id,
      type,
      displayName,
      namespace,
      properties,
      children,
      annotations,
      informativeScreenshot
    };
  }

  /**
   * „Éó„É≠„Éë„ÉÜ„Ç£„ÇíÊäΩÂá∫
   */
  private extractProperties(element: Element): Record<string, any> {
    const properties: Record<string, any> = {};

    // Â±ûÊÄß„Åã„Çâ„Éó„É≠„Éë„ÉÜ„Ç£„ÇíÂèñÂæó
    Array.from(element.attributes).forEach(attr => {
      if (attr.name !== 'DisplayName' && attr.name !== 'InformativeScreenshot') {
        properties[attr.name] = attr.value;
      }
    });

    // Â≠êË¶ÅÁ¥†„Åã„Çâ„Éó„É≠„Éë„ÉÜ„Ç£„ÇíÂèñÂæóÔºàAssign.To, Assign.ValueÁ≠âÔºâ
    Array.from(element.children).forEach(child => {
      const childName = child.localName;

      // „Éó„É≠„Éë„ÉÜ„Ç£Ë¶ÅÁ¥†Ôºà„Çø„Ç§„ÉóÂêç.„Éó„É≠„Éë„ÉÜ„Ç£ÂêçÂΩ¢ÂºèÔºâ
      if (childName.includes('.')) {
        const [, propName] = childName.split('.');
        properties[propName] = this.extractPropertyValue(child);
      }
    });

    return properties;
  }

  /**
   * „Éó„É≠„Éë„ÉÜ„Ç£ÂÄ§„ÇíÊäΩÂá∫
   */
  private extractPropertyValue(element: Element): any {
    // „ÉÜ„Ç≠„Çπ„Éà„Ç≥„É≥„ÉÜ„É≥„ÉÑ„Åå„ÅÇ„Çå„Å∞„Åù„Çå„ÇíËøî„Åô
    if (element.textContent?.trim()) {
      return element.textContent.trim();
    }

    // Â≠êË¶ÅÁ¥†„Åå„ÅÇ„Çå„Å∞ÊßãÈÄ†Âåñ„Åó„Å¶Ëøî„Åô
    const children = Array.from(element.children);
    if (children.length === 1) {
      const child = children[0];
      // OutArgument, InArgumentÁ≠â„ÅÆÂ†¥Âêà„ÅØ‰∏≠Ë∫´„ÇíËøî„Åô
      if (child.localName.includes('Argument')) {
        return child.textContent?.trim() || '';
      }
      // SelectorÁ≠â„ÅÆË§áÈõë„Å™ÊßãÈÄ†„ÅÆÂ†¥Âêà
      return this.elementToObject(child);
    }

    return null;
  }

  /**
   * Ë¶ÅÁ¥†„Çí„Ç™„Éñ„Ç∏„Çß„ÇØ„Éà„Å´Â§âÊèõ
   */
  private elementToObject(element: Element): any {
    const obj: any = {
      type: element.localName
    };

    // Â±ûÊÄß„ÇíËøΩÂä†
    Array.from(element.attributes).forEach(attr => {
      obj[attr.name] = attr.value;
    });

    // Â≠êË¶ÅÁ¥†„ÇíÂÜçÂ∏∞ÁöÑ„Å´Âá¶ÁêÜ
    const children = Array.from(element.children);
    if (children.length > 0) {
      obj.children = children.map(child => this.elementToObject(child));
    } else if (element.textContent?.trim()) {
      obj.value = element.textContent.trim();
    }

    return obj;
  }

  /**
   * Â≠ê„Ç¢„ÇØ„ÉÜ„Ç£„Éì„ÉÜ„Ç£„Çí„Éë„Éº„Çπ
   */
  private parseChildren(element: Element): Activity[] {
    const children: Activity[] = [];

    Array.from(element.children).forEach(child => {
      const childName = child.localName;

      // „Éó„É≠„Éë„ÉÜ„Ç£Ë¶ÅÁ¥†„ÅØÈô§Â§ñÔºà‰æã: NApplicationCard.BodyÔºâ
      if (childName.includes('.')) {
        // „Åü„Å†„Åó„ÄÅ„Éó„É≠„Éë„ÉÜ„Ç£Ë¶ÅÁ¥†„ÅÆ‰∏≠Ë∫´„ÅØÂÜçÂ∏∞ÁöÑ„Å´Âá¶ÁêÜ
        const nestedActivities = this.parseChildren(child);
        children.push(...nestedActivities);
        return;
      }

      // ActivityAction„Å™„Å©„ÅÆ„É©„ÉÉ„Éë„ÉºË¶ÅÁ¥†„ÅØ„ÄÅ‰∏≠Ë∫´„ÇíÂÜçÂ∏∞ÁöÑ„Å´Âá¶ÁêÜ
      if (this.isWrapperElement(child)) {
        const nestedActivities = this.parseChildren(child);
        children.push(...nestedActivities);
        return;
      }

      // „É°„Çø„Éá„Éº„ÇøË¶ÅÁ¥†„ÇíÈô§Â§ñ
      if (this.isMetadataElement(child)) {
        return;
      }

      // „Ç¢„ÇØ„ÉÜ„Ç£„Éì„ÉÜ„Ç£„Å®„Åó„Å¶Ëß£Êûê
      if (this.isActivity(child)) {
        children.push(this.parseActivity(child));
      }
    });

    return children;
  }

  /**
   * „É©„ÉÉ„Éë„ÉºË¶ÅÁ¥†„Åã„Å©„ÅÜ„Åã„ÇíÂà§ÂÆöÔºà„Ç¢„ÇØ„ÉÜ„Ç£„Éì„ÉÜ„Ç£„Åß„ÅØ„Å™„ÅÑ„Åå„ÄÅ‰∏≠Ë∫´„ÇíÂá¶ÁêÜ„Åô„ÇãÂøÖË¶Å„Åå„ÅÇ„ÇãË¶ÅÁ¥†Ôºâ
   */
  private isWrapperElement(element: Element): boolean {
    const name = element.localName;
    const wrapperTypes = [
      'ActivityAction',
      'ActivityAction.Argument'
    ];
    return wrapperTypes.includes(name);
  }

  /**
   * „É°„Çø„Éá„Éº„ÇøË¶ÅÁ¥†„Åã„Å©„ÅÜ„Åã„ÇíÂà§ÂÆöÔºà„Ç¢„ÇØ„ÉÜ„Ç£„Éì„ÉÜ„Ç£„Åß„ÅØ„Å™„ÅÑË¶ÅÁ¥†Ôºâ
   */
  private isMetadataElement(element: Element): boolean {
    const name = element.localName;

    // „É°„Çø„Éá„Éº„ÇøË¶ÅÁ¥†„ÅÆ„É™„Çπ„Éà
    const metadataTypes = [
      'WorkflowViewStateService.ViewState',
      'Dictionary',
      'Boolean',
      'String',
      'Property',
      'Variable',
      'InArgument',
      'OutArgument',
      'InOutArgument',
      'DelegateInArgument',
      'DelegateOutArgument',
      'TargetApp',
      'TargetAnchorable',
      'Target'
    ];

    // „Éó„É¨„Éï„Ç£„ÉÉ„ÇØ„Çπ„Åß„ÉÅ„Çß„ÉÉ„ÇØÔºàsap:, scg:, x: „Å™„Å©Ôºâ
    const prefix = element.prefix;
    if (prefix === 'sap' || prefix === 'sap2010' || prefix === 'scg' || prefix === 'sco' || prefix === 'x') {
      return true;
    }

    return metadataTypes.includes(name);
  }

  /**
   * „Ç¢„ÇØ„ÉÜ„Ç£„Éì„ÉÜ„Ç£Ë¶ÅÁ¥†„Åã„Å©„ÅÜ„Åã„ÇíÂà§ÂÆö
   */
  private isActivity(element: Element): boolean {
    const name = element.localName;

    // „Çà„Åè„ÅÇ„Çã„Ç¢„ÇØ„ÉÜ„Ç£„Éì„ÉÜ„Ç£„Çø„Ç§„ÉóÔºàÂü∫Êú¨Ôºâ
    const activityTypes = [
      'Sequence', 'Flowchart', 'StateMachine', 'Assign', 'If', 'While',
      'ForEach', 'Switch', 'TryCatch', 'Click', 'TypeInto', 'GetText',
      'LogMessage', 'WriteLine', 'InvokeWorkflowFile', 'Delay',
      // UiPath UIAutomation Next „Ç¢„ÇØ„ÉÜ„Ç£„Éì„ÉÜ„Ç£
      'NApplicationCard', 'NClick', 'NTypeInto', 'NGetText', 'NHover',
      'NKeyboardShortcut', 'NDoubleClick', 'NRightClick', 'NCheck',
      'NSelect', 'NAttach', 'NWaitElement', 'NFindElement',
      // „Åù„ÅÆ‰ªñ„ÅÆ„Çà„Åè„ÅÇ„ÇãUiPath„Ç¢„ÇØ„ÉÜ„Ç£„Éì„ÉÜ„Ç£
      'OpenBrowser', 'CloseBrowser', 'NavigateTo', 'AttachBrowser',
      'ReadRange', 'WriteRange', 'AddDataRow', 'BuildDataTable',
      'ForEachRow', 'ExcelApplicationScope', 'UseExcelFile'
    ];

    // „É™„Çπ„Éà„Å´„ÅÇ„Çå„Å∞„Ç¢„ÇØ„ÉÜ„Ç£„Éì„ÉÜ„Ç£
    if (activityTypes.includes(name)) {
      return true;
    }

    // „É°„Çø„Éá„Éº„ÇøË¶ÅÁ¥†„Åß„Å™„Åë„Çå„Å∞„Ç¢„ÇØ„ÉÜ„Ç£„Éì„ÉÜ„Ç£„Å®„Åó„Å¶Êâ±„ÅÜ
    return !this.isMetadataElement(element);
  }

  /**
   * Â§âÊï∞„ÇíÊäΩÂá∫
   */
  private extractVariables(root: Element): Variable[] {
    const variables: Variable[] = [];

    // <Sequence.Variables> Á≠â„ÇíÊé¢Á¥¢
    const variableElements = root.querySelectorAll('[x\\:TypeArguments]');

    variableElements.forEach(elem => {
      const name = elem.getAttribute('Name');
      const type = elem.getAttribute('x:TypeArguments');
      const defaultValue = elem.querySelector('Default')?.textContent?.trim();

      if (name && type) {
        variables.push({
          name,
          type,
          default: defaultValue
        });
      }
    });

    return variables;
  }

  /**
   * ÂºïÊï∞„ÇíÊäΩÂá∫
   */
  private extractArguments(root: Element): Argument[] {
    const argumentsData: Argument[] = [];

    // <x:Property> Ë¶ÅÁ¥†„ÇíÊé¢Á¥¢
    const propertyElements = root.querySelectorAll('x\\:Property');

    propertyElements.forEach(elem => {
      const name = elem.getAttribute('Name');
      const type = elem.getAttribute('Type');

      if (name && type) {
        // TypeÂ±ûÊÄß„Åã„ÇâÊñπÂêë„Å®„Éá„Éº„ÇøÂûã„ÇíÊäΩÂá∫Ôºà‰æã: InArgument(x:String)Ôºâ
        const match = type.match(/(In|Out|InOut)Argument\((.+)\)/);
        if (match) {
          argumentsData.push({
            name,
            type: match[1],            // In/Out/InOut
            dataType: match[2]         // „Éá„Éº„ÇøÂûã
          });
        }
      }
    });

    return argumentsData;
  }

  /**
   * „Ç¢„Éé„ÉÜ„Éº„Ç∑„Éß„É≥„ÇíÊäΩÂá∫
   */
  private extractAnnotations(element: Element): string | undefined {
    const annotationElem = element.querySelector('sap\\:WorkflowViewStateService\\.ViewState > scg\\:Dictionary > x\\:String[x\\:Key="Annotation"]');
    return annotationElem?.textContent?.trim();
  }
}
